







































import Vue from "vue";
import Modal from "./ProjectInfoModal.vue";
import Projects from "../../assets/Projects";

export default Vue.extend({
  data() {
    return {
      dialog: false,
      currentProject: 0,
      projects: this.project
    };
  },
  props: ["project", "assets"],
  components: {
    Modal
  },
  methods: {
    getThumbImage(index: number) {
      var images = require.context(`../../assets/`, false, /\.png$/);
      return images(`./` + `${this.assets}${index + 1}` + ".png");
    },
    hideModal(value: boolean) {
      this.dialog = value;
    },
    projectSelected() {
      return this.projects[this.currentProject];
    }
  }
});
