import Vue from "vue";
export default Vue.extend({
  data() {
    return {
      routes: [
        {
          route: "/",
          title: "About Me",
          icon: "account"
        },
        {
          route: "/projects",
          title: "Projects",
          icon: "wrench"
        },
        {
          route: "/resume",
          title: "Resume",
          icon: "file"
        },
        {
          route: "/contact",
          title: "Contact",
          icon: "contacts"
        },
        {
          route: "/fun",
          title: "Fun (Coming Soon)",
          icon: "image-filter-hdr"
        }
      ]
    };
  },
  computed: {
    image() {
      switch (this.$route.name) {
        case "fun":
          return "me4.jpg";
        default:
          return "me2.jpg";
      }
    }
  }
});
